<template>
  <div class="content">
        <h2>Carousel 轮播模板</h2>
        <!-- <p>Carousel 轮播图模板Carousel 轮播图模板Carousel 轮播图模板Carousel 轮播图模板Carousel 轮播图模板</p> -->

        <h3 style="margin:50px 0 20px">何时使用</h3>
        <ul>
            <!-- <li>网页中图片展示</li> -->
            <li>当有一组平级的内容。</li>
            <li>当内容空间不足时，可以用走马灯的形式进行收纳，进行轮播展现。</li>
            <li>常用于一组图片或卡片轮播</li>
        </ul>

        <h3 style="margin:50px 0 10px">
            基本代码结构
            <a-tag
                color="cyan"
                style="margin-left:10px;font-size:14px;cursor: pointer;"
                v-clipboard:copy="basic.html.copy" 
                v-clipboard:success="copySuccess" 
                v-clipboard:error="onError">复制代码</a-tag>
        </h3>
        <div class="codeView" v-highlight>
            <pre><code v-html="basic.html.code"></code></pre>
        </div>
        <h3 style="margin:50px 0 20px">效果演示</h3>
        <a-card v-for="(v,i) in demos" :key="i" style="margin-bottom:50px">
            <div class="effect-view" style="height:200px">
                <iframe class="effect" :src="v.src" frameborder="0"></iframe>
            </div>

            <a-divider orientation="left">{{v.title}}</a-divider>
            <p>{{v.desc}}</p>

            <a-divider orientation="left"><a-tag @click="onShowCode(i)" color="purple" style="cursor: pointer;">{{v.show?'关闭代码':'查看代码'}}</a-tag></a-divider>

            <div v-show="v.show" class="demo-area">
                <div v-if="v.html.copy" class="action">
                    <a-tag 
                    color="cyan"
                    style="cursor: pointer;"
                    v-clipboard:copy="v.html.copy" 
                    v-clipboard:success="copySuccess" 
                    v-clipboard:error="onError">复制代码</a-tag>
                    <span style="color:#999">HTML</span>
                </div>
                <div v-if="v.html.code" class="codeView" v-highlight>
                    <pre><code v-html="v.html.code"></code></pre>
                </div>

                <div v-if="v.css.copy" class="action">
                    <a-tag 
                    color="cyan"
                    style="cursor: pointer;"
                    v-clipboard:copy="v.css.copy" 
                    v-clipboard:success="copySuccess" 
                    v-clipboard:error="onError">复制代码</a-tag>
                    <span style="color:#999">CSS</span>
                </div>
                <div v-if="v.css.code" class="codeView" v-highlight>
                    <pre><code v-html="v.css.code"></code></pre>
                </div>

                <div v-if="v.js.copy" class="action">
                    <a-tag 
                    color="cyan"
                    style="cursor: pointer;"
                    v-clipboard:copy="v.js.copy" 
                    v-clipboard:success="copySuccess" 
                    v-clipboard:error="onError">复制代码</a-tag>
                    <span style="color:#999">JavaScript</span>
                </div>
                <div v-if="v.js.code" class="codeView" v-highlight>
                    <pre><code v-html="v.js.code"></code></pre>
                </div>
            </div>
            
        </a-card>

        <div class="api">
            <h3 style="margin:50px 0 20px">API</h3>
            <a-table :columns="apiColumns" :data-source="apiData" :pagination="false">
                <template slot="params" slot-scope="text">
                    <span style="color:#003a8c">{{text}}</span>
                </template>
                <template slot="desc" slot-scope="text">
                    <span>{{text}}</span>
                </template>
                <template slot="must" slot-scope="text">
                    <a-tag v-if="text==1" color="red">是 </a-tag>
                    <a-tag v-else color="green">否 </a-tag>
                </template>
                <template slot="default" slot-scope="text">
                    <span>{{text}}</span>
                </template>
            </a-table>
        </div>

        <div class="event">
            <h3 style="margin:50px 0 20px">事件</h3>
            <a-table :columns="eventColumns" :data-source="eventData" :pagination="false">
                <template slot="event" slot-scope="text">
                    <span style="color:#003a8c">{{text}}</span>
                </template>
                <template slot="desc" slot-scope="text">
                    <span>{{text}}</span>
                </template>
                <template slot="return" slot-scope="text">
                    <span>{{text}}</span>
                </template>
            </a-table>
        </div>
  </div>
</template>

<script>
import code from '@/code/carousels'
export default {
    data(){
        return{
            demos:[],
            basic:{},
            apiColumns:[],
            apiData:[],
            eventColumns:[],
            eventData:[],
        }
    },
    created(){
        this.demos = code.demos
        this.basic = code.basic
        this.apiColumns = code.api.columns
        this.apiData = code.api.data
        this.eventColumns = code.event.columns
        this.eventData = code.event.data
    },
    methods:{
        onShowCode(index){
            this.demos[index].show = !this.demos[index].show

        },
        copySuccess(){
            this.$message.success('复制成功');
        },
        onError(){
            this.$message.error('复制失败');
        }
    }
}
</script>

<style >

</style>



